@mixin paragraph() {
    font-size: 1.5em;
    line-height: 2;
    text-align: left;
}

@mixin add() {
    text-align: center;
    color: $text;
    background-color: $grey;
    border-radius: 15px;
    margin: 0 auto 4%;
    padding: 1% 0 0;
}




